// modals
.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(#001833, 0.9);
  @include vertical;
  z-index: 100;
  &.open {
    display: block; } }
.modal {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  opacity: 0;
  transform: translateY(100px);
  transition: transform 500ms, opacity 500ms;
  &.open {
    opacity: 1;
    transform: translateY(0); }
  .modal__in {
    position: relative;
    height: 100%;
    overflow-y: auto;
    z-index: 1;
    &:not(html.is-touch) & {
      padding-right: 8px;
      @include scrollbar {
        width: 8px;
        padding: 0 2px; }
      @include scrollbar-track {
        width: 4px;
        border-radius: 2px;
        background: #07386e; }
      @include scrollbar-thumb {
        width: 8px;
        border-radius: 4px;
        background: #bddcff; } } }
  .modal__img {
    max-width: 1400px;
    img {
      display: block;
      width: 100%;
      height: auto; } }
  .modal__close {
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px;
    background: #000;
    font-size: 0;
    color: $lightblue;
    text-transform: uppercase;
    z-index: 2;
    .icon {
      display: inline-block;
      vertical-align: middle;
      font-size: 34px; }
    @include r(768) {
      .icon {
        font-size: 30px; } } } }
